import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    AppGlobals.initialize(mapOf("title" to "RiverstoneHomesManagement"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.demo.riverstonehomesmanagement.pages.HomePage() }
        ctx.router.register("/about") { com.demo.riverstonehomesmanagement.pages.AboutPage() }
        ctx.router.register("/contact") { com.demo.riverstonehomesmanagement.pages.ContactPage() }
        ctx.router.register("/offerings") { com.demo.riverstonehomesmanagement.pages.OfferingsPage()
                }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text",
                com.demo.riverstonehomesmanagement.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text",
                com.demo.riverstonehomesmanagement.SubheadlineTextStyle)
        ctx.theme.registerStyle("markdown",
                com.demo.riverstonehomesmanagement.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle("page-content",
                com.demo.riverstonehomesmanagement.components.layouts.PageContentStyle)
        ctx.theme.registerStyle("footer",
                com.demo.riverstonehomesmanagement.components.sections.FooterStyle)
        ctx.theme.registerStyle("nav-header",
                com.demo.riverstonehomesmanagement.components.sections.NavHeaderStyle)
        ctx.theme.registerStyle("arrow-up",
                com.demo.riverstonehomesmanagement.components.widgets.ArrowUpStyle)
        ctx.theme.registerStyle("back-to-top-button",
                com.demo.riverstonehomesmanagement.components.widgets.BackToTopButtonStyle)
        ctx.theme.registerStyle("hero-container",
                com.demo.riverstonehomesmanagement.pages.HeroContainerStyle)
        ctx.theme.registerStyle("about-top-section",
                com.demo.riverstonehomesmanagement.theme.styles.about.AboutTopSectionStyle)
        ctx.theme.registerStyle("about-top-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.AboutTopTitleTextStyle)
        ctx.theme.registerStyle("belief-item",
                com.demo.riverstonehomesmanagement.theme.styles.about.BeliefItemStyle)
        ctx.theme.registerStyle("belief-item-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.BeliefItemTitleTextStyle)
        ctx.theme.registerStyle("belief-item-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.BeliefItemSubTitleTextStyle)
        ctx.theme.registerStyle("landing-section",
                com.demo.riverstonehomesmanagement.theme.styles.about.LandingSectionStyle)
        ctx.theme.registerStyle("landing-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.LandingTitleTextStyle)
        ctx.theme.registerStyle("landing-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.LandingSubTitleTextStyle)
        ctx.theme.registerStyle("our-beliefs-section",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurBeliefsSectionStyle)
        ctx.theme.registerStyle("our-beliefs-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurBeliefsTitleTextStyle)
        ctx.theme.registerStyle("our-beliefs-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurBeliefsSubTitleTextStyle)
        ctx.theme.registerStyle("our-story-about-section",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurStoryAboutSectionStyle)
        ctx.theme.registerStyle("our-story-about-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurStoryAboutTitleTextStyle)
        ctx.theme.registerStyle("our-story-about-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurStoryAboutSubTitleTextStyle)
        ctx.theme.registerStyle("our-story-about-description-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurStoryAboutDescriptionTextStyle)
        ctx.theme.registerStyle("our-story-section",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurStorySectionStyle)
        ctx.theme.registerStyle("our-story-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurStoryTitleTextStyle)
        ctx.theme.registerStyle("our-story-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.OurStorySubTitleTextStyle)
        ctx.theme.registerStyle("service-section",
                com.demo.riverstonehomesmanagement.theme.styles.about.ServiceSectionStyle)
        ctx.theme.registerStyle("service-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.ServiceTitleTextStyle)
        ctx.theme.registerStyle("service-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.ServiceSubTitleTextStyle)
        ctx.theme.registerStyle("transform-home-section",
                com.demo.riverstonehomesmanagement.theme.styles.about.TransformHomeSectionStyle)
        ctx.theme.registerStyle("transform-home-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.TransformHomeTitleTextStyle)
        ctx.theme.registerStyle("transform-home-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.TransformHomeSubTitleTextStyle)
        ctx.theme.registerStyle("why-choose-us-section",
                com.demo.riverstonehomesmanagement.theme.styles.about.WhyChooseUsSectionStyle)
        ctx.theme.registerStyle("why-choose-us-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.WhyChooseUsTitleTextStyle)
        ctx.theme.registerStyle("why-choose-us-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.about.WhyChooseUsSubTitleTextStyle)
        ctx.theme.registerStyle("service-item",
                com.demo.riverstonehomesmanagement.theme.styles.offerings.ServiceItemStyle)
        ctx.theme.registerStyle("service-item-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.offerings.ServiceItemTitleTextStyle)
        ctx.theme.registerStyle("service-item-sub-title-text",
                com.demo.riverstonehomesmanagement.theme.styles.offerings.ServiceItemSubTitleTextStyle)
        ctx.theme.registerVariant("-circle", com.demo.riverstonehomesmanagement.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored",
                com.demo.riverstonehomesmanagement.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("side-menu-slide-in",
                com.demo.riverstonehomesmanagement.components.sections.SideMenuSlideInAnim)
        ctx.theme.registerKeyframes("fade-in-key-frames",
                com.demo.riverstonehomesmanagement.components.widgets.FadeInKeyFrames)
        com.demo.riverstonehomesmanagement.initColorMode(ctx)
        com.demo.riverstonehomesmanagement.initSiteStyles(ctx)
        com.demo.riverstonehomesmanagement.theme.initTheme(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "root") {
        com.demo.riverstonehomesmanagement.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
